export default {
	data() {
		return {
			active:1,
			activeClass:"info-header-btn-active",
			supplyList:[],
			supplyList2:[],
			shrink:"",
			id:"",
			branch:0,
		}
	},
	onLoad:function(option){
		var _self = this
		_self.id = option.id
		_self.branch = option.branch
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/declareQuery",
				data:{id:"0",mark:1,optionId:uni.getStorageSync("openId"),branch:_self.id}, 
				method:"GET",
				success:function(res){
					_self.supplyList = res.data
					_self.shrink = res.data
					console.log(res.data) 
				} 
			})
		},

		lookDetail:function(id,mark,branch){
			console.log(branch)
			uni.navigateTo({
				url:"../../infoPage/infoDetail/infoDetail?id="+id+"&mark="+mark+"&branch="+branch
			})
		}
	},
	filters:{
		getPic:function(res){
			return res.split(',')[0]
		},
		getTime:function(res){
			return res.slice(0,11)
		}
	}
}
